(function() {
  this.App.controller('DashboardController', ["$scope", "$cookies", "$sce", "MnoeMarketplace", "MnoErrorsHandler", "MnoeCurrentUser", "STAFF_PAGE_AUTH", function($scope, $cookies, $sce, MnoeMarketplace, MnoErrorsHandler, MnoeCurrentUser, STAFF_PAGE_AUTH) {
    'ngInject';
    var main, mobileView;
    main = this;
    main.errorHandler = MnoErrorsHandler;
    main.staffPageAuthorized = STAFF_PAGE_AUTH;
    main.trustSrc = function(src) {
      return $sce.trustAsResourceUrl(src);
    };
    mobileView = 992;
    main.getWidth = function() {
      return window.innerWidth;
    };
    $scope.$watch(main.getWidth, function(newValue) {
      if (newValue >= mobileView) {
        if (angular.isDefined($cookies.get('admin_platform_toggle'))) {
          return main.toggle = !$cookies.get('admin_platform_toggle') ? false : true;
        } else {
          return main.toggle = true;
        }
      } else {
        return main.toggle = false;
      }
    });
    main.toggleSidebar = function() {
      main.toggle = !main.toggle;
      return $cookies.put('admin_platform_toggle', main.toggle);
    };
    window.onresize = function() {
      return $scope.$apply();
    };
    MnoeCurrentUser.getAdminRole().then(main.user = MnoeCurrentUser.user);
  }]);

}).call(this);



(function() {
  this.App.service('MnoAppsInstances', function() {
    var _self;
    _self = this;
    this.isConnected = function(instance) {
      return instance.stack === 'cube' || instance.stack === 'cloud' || _self.isOauthConnected(instance);
    };
    this.isOauthConnected = function(instance) {
      return instance.stack === 'connector' && instance.oauth_keys_valid;
    };
    this.oAuthConnectPath = function(instance) {
      var redirect;
      redirect = window.encodeURIComponent("" + location.pathname + location.hash);
      return "/mnoe/webhook/oauth/" + instance.uid + "/authorize?redirect_path=" + redirect;
    };
    this.canBeDataSynced = function(instance) {
      return instance.stack === 'connector' && instance.oauth_keys_valid;
    };
    this.dataSyncPath = function(instance) {
      return "/mnoe/webhook/oauth/" + instance.uid + "/sync";
    };
    this.canBeDisconnected = function(instance) {
      return instance.stack === 'connector' && instance.oauth_keys_valid;
    };
    this.disconnectPath = function(instance) {
      return "/mnoe/webhook/oauth/" + instance.uid + "/disconnect";
    };
    return this;
  });

}).call(this);



(function() {
  this.App.service('MnoErrorsHandler', ["$log", function($log) {
    var _self, errorCache;
    _self = this;
    errorCache = null;
    this.processServerError = function(serverError, form) {
      var ref;
      $log.error('An error occurred:', serverError);
      if (_.startsWith(serverError.data, '<!DOCTYPE html>')) {
        return;
      }
      if ((400 <= (ref = serverError.status) && ref <= 499)) {
        errorCache = serverError;
        return _.each(errorCache.data, function(errors, key) {
          return _.each(errors, function() {
            if (form[key] != null) {
              return form[key].$setValidity('server', false);
            } else {
              return $log.error('MnoErrorsHandler: cannot find field:' + key);
            }
          });
        });
      }
    };
    this.errorMessage = function(name) {
      var result;
      result = [];
      if (errorCache != null) {
        _.each(errorCache.data[name], function(msg) {
          return result.push(msg);
        });
      }
      return result.join(', ');
    };
    this.resetErrors = function(form) {
      if (errorCache != null) {
        _.each(errorCache.data, function(errors, key) {
          return _.each(errors, function() {
            if (form[key] != null) {
              return form[key].$setValidity('server', null);
            }
          });
        });
        return errorCache = null;
      }
    };
    this.onlyServerError = function(form) {
      return _.every(form.$error, function(v, k) {
        return k === 'server';
      });
    };
    return this;
  }]);

}).call(this);



(function() {
  this.App.service('MnoConfirm', ["$uibModal", function($uibModal) {
    var _self, modalDefaults, modalOptions;
    _self = this;
    modalOptions = {
      closeButtonText: 'Close',
      actionButtonText: 'OK',
      headerText: 'Proceed?',
      bodyText: 'Perform this action?'
    };
    modalDefaults = {
      backdrop: true,
      keyboard: true,
      modalFade: true,
      templateUrl: 'app/components/mno-confirm/mno-confirm.html'
    };
    this.showModal = function(customModalOptions, customModalDefaults) {
      if (customModalDefaults == null) {
        customModalDefaults = null;
      }
      if (customModalDefaults == null) {
        customModalDefaults = {};
      }
      customModalDefaults.backdrop = 'static';
      return _self.show(customModalOptions, customModalDefaults);
    };
    this.show = function(customModalOptions, customModalDefaults) {
      var tempModalDefaults, tempModalOptions;
      if (customModalDefaults == null) {
        customModalDefaults = null;
      }
      tempModalDefaults = {};
      tempModalOptions = {};
      angular.extend(tempModalOptions, modalOptions, customModalOptions);
      angular.extend(tempModalDefaults, modalDefaults, customModalDefaults);
      if (!tempModalDefaults.controller) {
        tempModalDefaults.controller = ["$scope", "$uibModalInstance", function($scope, $uibModalInstance) {
          'ngInject';
          $scope.modalOptions = tempModalOptions;
          $scope.modalOptions.ok = function(result) {
            return $uibModalInstance.close(result);
          };
          return $scope.modalOptions.close = function(result) {
            return $uibModalInstance.dismiss('cancel');
          };
        }];
      }
      return $uibModal.open(tempModalDefaults).result;
    };
    return this;
  }]);

}).call(this);



