(function() {
  this.App.directive('mnoeOrganizationsList', ["$filter", "$log", "MnoeOrganizations", function($filter, $log, MnoeOrganizations) {
    return {
      restrict: 'E',
      scope: {
        list: '='
      },
      templateUrl: 'app/components/mnoe-organizations-list/mnoe-organizations-list.html',
      link: function(scope, elem, attrs) {
        var displayCurrentState, fetchOrganizations, setAllOrganizationsList, setLastOrganizationsList, setSearchOrganizationsList;
        scope.state = attrs.view;
        scope.organizations = {
          search: '',
          nbItems: 10,
          page: 1,
          pageChangedCb: function(nbItems, page) {
            var offset;
            scope.organizations.nbItems = nbItems;
            scope.organizations.page = page;
            offset = (page - 1) * nbItems;
            return fetchOrganizations(nbItems, offset);
          }
        };
        fetchOrganizations = function(limit, offset, sort) {
          if (sort == null) {
            sort = 'name';
          }
          scope.organizations.loading = true;
          return MnoeOrganizations.list(limit, offset, sort).then(function(response) {
            scope.organizations.totalItems = response.headers('x-total-count');
            return scope.organizations.list = response.data;
          }).then(function() {
            return scope.organizations.loading = false;
          });
        };
        scope.switchState = function() {
          scope.state = attrs.view = attrs.view === 'all' ? 'last' : 'all';
          return displayCurrentState();
        };
        displayCurrentState = function() {
          if (attrs.view === 'all') {
            setAllOrganizationsList();
            return fetchOrganizations(scope.organizations.nbItems, 0);
          } else if (attrs.view === 'last') {
            setLastOrganizationsList();
            return fetchOrganizations(scope.organizations.nbItems, 0, 'created_at.desc');
          } else {
            return $log.error('Value of attribute view can only be "all" or "last"');
          }
        };
        setAllOrganizationsList = function() {
          scope.organizations.widgetTitle = 'All organisations';
          return scope.organizations.switchLinkTitle = '(last 10)';
        };
        setLastOrganizationsList = function() {
          scope.organizations.widgetTitle = 'Last 10 organisations';
          return scope.organizations.switchLinkTitle = '(view all)';
        };
        scope.searchChange = function() {
          if (scope.organizations.search.length >= 3) {
            scope.searchMode = true;
            return setSearchOrganizationsList(scope.organizations.search);
          } else if (scope.searchMode) {
            scope.searchMode = false;
            return displayCurrentState();
          }
        };
        setSearchOrganizationsList = function(search) {
          var terms;
          scope.organizations.loading = true;
          scope.organizations.widgetTitle = 'Search result';
          delete scope.organizations.switchLinkTitle;
          search = scope.organizations.search.toLowerCase();
          terms = {
            'name.like': "%" + search + "%"
          };
          return MnoeOrganizations.search(terms).then(function(response) {
            scope.organizations.totalItems = response.headers('x-total-count');
            return scope.organizations.list = $filter('orderBy')(response.data, 'name');
          })["finally"](function() {
            return scope.organizations.loading = false;
          });
        };
        return displayCurrentState();
      }
    };
  }]);

}).call(this);



(function() {
  this.App.directive('mnoeOrganizationsLocalList', ["$filter", "$log", function($filter, $log) {
    return {
      restrict: 'E',
      scope: {
        list: '='
      },
      templateUrl: 'app/components/mnoe-organizations-local-list/mnoe-organizations-local-list.html',
      link: function(scope, elem, attrs) {
        var displayNormalState, setAllOrganizationsList, setLastOrganizationsList, setSearchOrganizationsList;
        scope.organizations = {
          displayList: [],
          widgetTitle: 'Loading organizations...',
          search: ''
        };
        setAllOrganizationsList = function() {
          scope.organizations.widgetTitle = 'All organizations (' + scope.list.length + ')';
          scope.organizations.switchLinkTitle = '(last 10)';
          return scope.organizations.displayList = $filter('orderBy')(scope.list, 'email');
        };
        setLastOrganizationsList = function() {
          scope.organizations.widgetTitle = 'Last 10 organizations';
          scope.organizations.switchLinkTitle = '(view all)';
          scope.organizations.displayList = $filter('orderBy')(scope.list, '-created_at');
          return scope.organizations.displayList = $filter('limitTo')(scope.organizations.displayList, 10);
        };
        setSearchOrganizationsList = function() {
          var searchToLowerCase;
          scope.organizations.widgetTitle = 'Search result';
          delete scope.organizations.switchLinkTitle;
          searchToLowerCase = scope.organizations.search.toLowerCase();
          scope.organizations.displayList = _.filter(scope.list, function(user) {
            var email, name, surname;
            if (user.email) {
              email = _.contains(user.email.toLowerCase(), searchToLowerCase);
            }
            if (user.name) {
              name = _.contains(user.name.toLowerCase(), searchToLowerCase);
            }
            if (user.surname) {
              surname = _.contains(user.surname.toLowerCase(), searchToLowerCase);
            }
            return email || name || surname;
          });
          return scope.organizations.displayList = $filter('orderBy')(scope.organizations.displayList, 'email');
        };
        displayNormalState = function() {
          if (attrs.view === 'all') {
            return setAllOrganizationsList();
          } else if (attrs.view === 'last') {
            return setLastOrganizationsList();
          } else {
            return $log.error('Value of attribute view can only be "all" or "last"');
          }
        };
        scope.switchState = function() {
          if (attrs.view === 'all') {
            attrs.view = 'last';
          } else {
            attrs.view = 'all';
          }
          return displayNormalState();
        };
        scope.searchChange = function() {
          if (scope.organizations.search === '') {
            return displayNormalState();
          } else {
            return setSearchOrganizationsList();
          }
        };
        return scope.$watch('list', function(newVal) {
          if (newVal) {
            return displayNormalState();
          }
        }, true);
      }
    };
  }]);

}).call(this);



(function() {
  this.App.component('mnoeStaffsList', {
    templateUrl: 'app/components/mnoe-staffs-list/mnoe-staffs-list.html',
    bindings: {
      view: '@'
    },
    controller: ["$filter", "$log", "MnoeUsers", "MnoConfirm", "MnoeObservables", "ADMIN_ROLES", "OBS_KEYS", "toastr", function($filter, $log, MnoeUsers, MnoConfirm, MnoeObservables, ADMIN_ROLES, OBS_KEYS, toastr) {
      var fetchStaffs, updateSearch, updateSort, vm;
      vm = this;
      vm.listOfStaff = [];
      vm.callServer = function(tableState) {
        var search, sort;
        sort = updateSort(tableState.sort);
        search = updateSearch(tableState.search);
        return fetchStaffs(vm.staff.nbItems, vm.staff.offset, sort, search);
      };
      updateSort = function(sortState) {
        var sort;
        if (sortState == null) {
          sortState = {};
        }
        sort = "surname";
        if (sortState.predicate) {
          sort = sortState.predicate;
          if (sortState.reverse) {
            sort += ".desc";
          } else {
            sort += ".asc";
          }
        }
        vm.staff.sort = sort;
        return sort;
      };
      updateSearch = function(searchingState) {
        var attr, ref, search, value;
        if (searchingState == null) {
          searchingState = {};
        }
        search = {};
        if (searchingState.predicateObject) {
          ref = searchingState.predicateObject;
          for (attr in ref) {
            value = ref[attr];
            if (attr === "admin_role") {
              search['where[admin_role.in][]'] = [value];
            } else {
              search['where[' + attr + '.like]'] = value + '%';
            }
          }
        }
        vm.staff.search = search;
        return search;
      };
      vm.state = vm.view;
      vm.staff = {
        editmode: [],
        search: {},
        sort: "surname",
        nbItems: 10,
        page: 1,
        roles: ADMIN_ROLES,
        pageChangedCb: function(nbItems, page) {
          var offset;
          vm.staff.nbItems = nbItems;
          vm.staff.page = page;
          offset = (page - 1) * nbItems;
          return fetchStaffs(nbItems, offset);
        },
        update: function(staff) {
          return MnoeUsers.updateStaff(staff).then(function(response) {
            updateSort();
            updateSearch();
            return delete vm.staff.editmode[staff.id];
          }, function(error) {
            $log.error('Error while saving user', error);
            return toastr.error('An error occurred while saving the user.');
          });
        },
        remove: function(staff) {
          var modalOptions;
          modalOptions = {
            closeButtonText: 'Cancel',
            actionButtonText: 'Delete Team Member',
            headerText: 'Delete ' + staff.name + ' ' + staff.surname + '?',
            bodyText: 'Are you sure you want to delete this team member?'
          };
          return MnoConfirm.showModal(modalOptions).then(function() {
            console.log('Remove staff role:' + staff);
            return MnoeUsers.removeStaff(staff.id).then(function() {
              return toastr.success(staff.name + " " + staff.surname + " has been successfully removed.");
            });
          });
        }
      };
      fetchStaffs = function(limit, offset, sort, search) {
        if (sort == null) {
          sort = vm.staff.sort;
        }
        if (search == null) {
          search = vm.staff.search;
        }
        vm.staff.loading = true;
        return MnoeUsers.staffs(limit, offset, sort, search).then(function(response) {
          vm.staff.totalItems = response.headers('x-total-count');
          return vm.listOfStaff = response.data;
        })["finally"](function() {
          return vm.staff.loading = false;
        });
      };
      fetchStaffs(vm.staff.nbItems, 0).then(function() {
        MnoeObservables.registerCb(OBS_KEYS.staffAdded, function() {
          return fetchStaffs(vm.staff.nbItems, vm.staff.offset);
        });
        return MnoeObservables.registerCb(OBS_KEYS.staffChanged, function() {
          return fetchStaffs(vm.staff.nbItems, vm.staff.offset);
        });
      });
    }]
  });

}).call(this);



